import { useState } from 'react';
import { supabase } from '../../../shared/services/supabase';

export function useFetchFavoriteRecipes() {
  const [currentSkip, setCurrentSkip] = useState(0);
  const [currentPageSize] = useState(10);
  const [recipes, setRecipes] = useState([]);
  const [hasMore, setHasMore] = useState(true);

  const [loading, setLoading] = useState(false);
  const [totalCount, setTotalCount] = useState(0);

  const loadMoreRecipes = async () => {
    if (loading || !hasMore) return;
    setLoading(true);
    try {
      const { data: { session } } = await supabase.auth.getSession();
      const userId = session.user.id;

      const { data, count, error } = await supabase
        .from('recipes')
        .select('*, favorites!inner(user_id)', { count: 'exact' })
        .eq('favorites.user_id', userId) // Filter favorites by user ID
        .range(currentSkip, currentSkip + currentPageSize - 1);

      if (error) {
        throw error;
      }
      if (data.length === 0) {
        setHasMore(false);
      } else {
        setRecipes((prev) => [...prev, ...data]);
        setCurrentSkip((prevSkip) => prevSkip + data.length);
      }

      setTotalCount(count);
    } catch (error) {
      console.error('Error fetching favorite recipes:', error.message);
    } finally {
      setLoading(false);
    }
  };
  return { recipes, loading, totalCount, loadMoreRecipes };
}
